
import {defineComponent, ref} from "vue";
import {useStore} from "vuex";
import {FlexScheduleCellSetting, SortOptions} from "@/store/flexScheduleModule";
import {nextTick} from "@vue/runtime-core";
import {Icon} from "@iconify/vue";

export default defineComponent({
    name: "FlexScheduleMultiColumn",
    components: {
        Icon
    },

    setup() {
        const store = useStore()
        const open = ref(false)
        const optionSelected = ref([])

        // const costCenters = store.state.LookUp.costCenters;

        const selectionOption = ref(<SortOptions[]>[]);

        selectionOption.value = store.state.FlexSchedule.selectableColumn

        return {open,  optionSelected, selectionOption}
    },
    methods: {
        checkOptions(option: any) {
            // todo: trigger when user click on the row
            option.selected = !option.selected
            this.$emit('optionChanged')
        },
        selectSort(option: any) {
            switch (option.sort) {
                case 'Ascend':
                    option.sort = 'Descend'
                    break;
                case 'Descend':
                    option.sort = ''
                    break;
                case '':
                    option.sort = 'Ascend'
                    break;
                default:
                    console.error('select sort error state')

            }
            this.$emit('sortChanged')
        }
    }

})
